// AdminDashboard.js

import React, {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import {toast} from "react-toastify";

import "../assets/css/AdminDashboard.css";

import logo from "../assets/images/image.png";

import Sidebar from "../components/Sidebar";
import Loading from "../components/Loading";

import {postRequest} from "../routes/Routes";
import {getUser} from "../auth/Auth";
import {loadTShirt} from "../common/Common";

const AddTshirts = () => {
    const navigate = useNavigate();
    const [user, setUser] = useState(null),
        [loading, setLoading] = useState(false),
        [loadingContent, setLoadingContent] = useState(false),
        [tShirtId] = useState(decodeURI(window.location.href.split('/').at(-1)));

    const [formData, setFormData] = useState({
        EditTshirtSize: '',
        EditDescription: '',
        TshirtQty: '',
        tshirtSize: '',
    });

    useEffect(() => {
        getUser().then((data) => {
            setUser(data);
        }).catch(() => {
            setUser(null);
        });
        if (tShirtId !== 'add') {
            setLoadingContent(true);
            loadTShirt(tShirtId).then(data => {
                const tempFormData = {
                    EditDescription: data.description,
                    TshirtQty: data.quantity,
                    tshirtSize: data.size,
                    id: data.id
                }
                setFormData(tempFormData);
                setLoadingContent(false);
            }).catch(() => null);
        }
    }, []);

    const handleChange = (e) => {
        const {name, value} = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleSubmit = (e) => {
        setLoading(true);
        e.preventDefault();
        let values = {
            size: formData.tshirtSize,
            description: formData.EditDescription,
            quantity: formData.TshirtQty,
            token: user.token,
            endpoint: formData.hasOwnProperty("id") ? '/api/t-shirt/update/' + formData.id : '/api/t-shirt'
        };
        postRequest(values).then(response => {
            if (response.status === 200) {
                if (response.data.error) {
                    toast.error(response.data.message, {
                        position: "bottom-center",
                        autoClose: 3000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                } else {
                    toast.success(response.data.message, {
                        position: "bottom-center",
                        autoClose: 3000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                    navigate('/admin/dashboard/tshirt/overview');
                }
            } else {
                toast.error("An error occurred!", {
                    position: "bottom-center",
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
            setLoading(false);
        });
    };

    const handleCancel = () => {
        navigate('/admin/dashboard/tshirt/overview');
    };

    if (!loadingContent && user) {
        return (
            <div className="admin-dashboard">
                <Sidebar/>
                <div className="main-content">
                    <div className="cont-wrapper">
                        <div className="dashboardText">
                            <h1>T-Shirts</h1>
                            <span>Add/ Remove / Edit T-shirts</span>
                        </div>
                        <div className="form-container">
                            <form onSubmit={handleSubmit}>
                                <div className="frm-row single-row">
                                    <div className="one-row">
                                        <label htmlFor="tshirtSize">T-Shirt Size</label>
                                        <input type="text" id="tshirtSize" name="tshirtSize"
                                               value={formData.tshirtSize} onChange={handleChange} placeholder="s"
                                               required/>
                                    </div>
                                    <div className="one-row">
                                        <label htmlFor="EditDescription">Description</label>
                                        <input type="text" id="EditDescription" name="EditDescription"
                                               value={formData.EditDescription} onChange={handleChange}
                                               placeholder="Small in size" required/>
                                    </div>
                                </div>
                                <div className="frm-row single-row">
                                    <div className="one-row halfRow">
                                        <label htmlFor="TshirtQty">Quantity</label>
                                        <input type="number" id="TshirtQty" name="TshirtQty" value={formData.TshirtQty}
                                               onChange={handleChange} placeholder="555" required/>
                                    </div>
                                </div>

                                <div className="buttin-set-sub">
                                    <button type="button" className="onFormCancle" onClick={handleCancel}>Cancel
                                    </button>
                                    <button type="submit" className="onFormSubmit">
                                        {loading ?
                                            <span className="spinner-grow spinner-grow-sm ml-1" role="status"
                                                  aria-hidden="true"/>
                                            : null
                                        }
                                        Save
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>

                {/* profile section */}
                <div className="profile-section">
                    <img src={logo} alt="Admin Profile" className="profile-image"/>
                    <p className="admin-name">Welcome {user.name}</p>
                </div>
            </div>
        );
    } else {
        return (
            <Loading/>
        );
    }
};

export default AddTshirts;
